
import headTop from '@/components/header'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters, mapMutations } from 'vuex'
import Api from '@/js/common'
import single from '@/pages/questionType/single'
import double from '@/pages/questionType/double'
import downSelect from '@/pages/questionType/downSelect'
import singleLine from '@/pages/questionType/singleLine'
import multiLine from '@/pages/questionType/multiLine'

export default {
data() {
return {
status: '',//试卷状态 0未过期，1过期未答，2是已答
questionList: [],
title: '',
endTime: '',
endWord: '',
random:'',//接口返回的随机数
ToastShow:false,
toastInfo:"",
mustBeId:[],//必答题的id
describe:""
}
},
computed: {
...mapGetters(['device']),
...mapGetters('submit', ['answer'])
},
methods: {
getDetail() {
let vmSelf = this
var submitObj = {
ques_id: vmSelf.$route.params.id,
ques_wj: vmSelf.$route.params.random
}
vmSelf.$vux.loading.show({
text: 'Loading'
})
Api.replayDetail(submitObj)
.then(function(response) {
vmSelf.$vux.loading.hide()
if(response.status == 1){
if(response.data.isReply == 0){
vmSelf.status = 1
}else{
vmSelf.status = 2
}
}else if(response.status == 2){
vmSelf.status = response.status
}else{
vmSelf.status = response.statusstatus
}
vmSelf.title = response.data.ques.title
vmSelf.describe = response.data.ques.descr
vmSelf.endWord = response.data.ques.questionnaire
vmSelf.endTime = response.data.ques.expire_date
vmSelf.questionList = response.data.ques_bank
for(var i in vmSelf.questionList){
if(vmSelf.questionList[i].status=="0"){
vmSelf.mustBeId.push(vmSelf.questionList[i].id)
}
}
vmSelf.random = response.data.ques.ques_wj
})
.catch(function(err) {
console.log('=====', err)
})
},
submitInfo(){
for(var i in this.mustBeId){
var mustIndex = this.mustBeId[i]
if(!this.answer[mustIndex]){
this.alertInfo("您有必答题未提交")
return
}
}
let vmSelf = this
var submitObj = {
ques_id: vmSelf.$route.params.id,
ques_wj: vmSelf.$route.params.random,
reply:JSON.stringify(this.answer)
}

vmSelf.$vux.loading.show({
text: 'Loading'
})
Api.submitInfo(submitObj)
.then(function(response) {
vmSelf.$vux.loading.hide()
vmSelf.toastInfo = response
vmSelf.ToastShow = true
setTimeout(function(){
vmSelf.$router.go(-1)
},3000)
})
.catch(function(err) {
console.log('=====', err)
})
},
alertInfo(title) {
var vmSelf=this
this.$vux.alert.show({
title: '提示',
content: title,
onShow() {
console.log("Plugin: I'm showing")
},
onHide() {
console.log("Plugin: I'm hiding now")
}
})
},
changeTimeType(time){
return Api.changeTimeType(time)
}
},
created() {
this.getDetail()
},
components: {
ViewBox,
headTop,
single,
double,
downSelect,
singleLine,
multiLine,
XButton,
Toast
},
mounted() {
appcan.setNavigationBarTitle({
title: "title"
});
},
}



